<script>
import axios from 'axios';
export default {
  name: 'HomePage',
  data() {
    return {
      input: '',
      messages: [
        {
          role: 'bot',
          text: '‰Ω†Â•ΩÔºÅÊàëÊòØ‰Ω†ÁöÑConflict SimulationÂä©Êâã„ÄÇÊàëÂèØ‰ª•Â∏ÆÂä©‰Ω†Ê®°ÊãüÂíåÂàÜÊûêÂêÑÁßçÂÜ≤Á™ÅÂú∫ÊôØÔºåÂπ∂Êèê‰æõ‰∏™ÊÄßÂåñÁöÑÂèçÈ¶à„ÄÇ'
        }
      ],
      loading: false,
      scoreFeedback: null,
      teachFeedback: [],
      scoreFeedbackLoading: false,
      teachFeedbackLoading: false,
      showSettingsModal: false,
      currentTraining: 'ÂºïÂØºÊÄßÂØπËØù',
      currentTitle: 'Èõ∂Ëä±Èí±ÁöÑ‰ΩøÁî®',
      conversation_id: "",
      score_id:"",
      teach_id:"",
      quit:0,
    }
  },
  computed: {
    lastTime() {
      return '17:25';
    }
  },
  methods: {
    async sendMessage() {
      const text = this.input && this.input.trim();
      if (!text || this.loading) return;
      const title = this.currentTitle; // ‰ΩøÁî®Âä®ÊÄÅtitle
      console.log(this.currentTitle);
      console.log(this.currentTraining);
      this.messages.push({ role: 'user', text });
      this.input = '';
      this.loading = true;
      this.$nextTick(() => {
        const chatHistory = this.$el.querySelector('.chat-history');
        if (chatHistory) chatHistory.scrollTop = chatHistory.scrollHeight;
      });
      // ÂÖàÊèíÂÖ•‰∏Ä‰∏™Á©∫ÁöÑÊú∫Âô®‰∫∫Ê∞îÊ≥°
      const botMsg = { role: 'bot', text: '' };
      this.messages.push(botMsg);
      this.$nextTick(() => {
        const chatHistory = this.$el.querySelector('.chat-history');
        if (chatHistory) chatHistory.scrollTop = chatHistory.scrollHeight;
      });
      let botResponse = '';
      try {
        const response = await fetch('http://dify.ai-role.cn/v1/chat-messages', {
          method: 'POST',
          headers: {
            'Authorization': 'Bearer app-1FfMKOZZ12LF8HXlpXdTopPG',
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            query: text,
            inputs: {
              training: this.currentTraining, // ‰ΩøÁî®Âä®ÊÄÅtraining
              title: title,
              quit:this.quit
            },
            response_mode: "streaming",
            user: "abc-123",
            conversation_id:this.conversation_id
          })
        });
        if (!response.body || !window.ReadableStream) {
          // fallback: ‰∏çÊòØÊµÅÂºèÂìçÂ∫î
          const data = await response.json();
          botMsg.text = data.answer || data.response || 'ÔºàÂ§ßÊ®°ÂûãÊó†ÂõûÂ§çÔºâ';
          botResponse = botMsg.text;
          
          // Ëé∑Âèñconversation_id
          if (data.conversation_id) {
            this.conversation_id = data.conversation_id;
          }
          
          this.loading = false;
          // Êú∫Âô®‰∫∫ÂõûÂ§çÂêéÔºåÊãâÂèñÂèçÈ¶à
          this.get_score_feedback(title, text, botResponse);
          this.get_teach_feedback(title, text, botResponse);
          return;
        }
        const reader = response.body.getReader();
        // console.log(response);
        const decoder = new TextDecoder('utf-8');
        let done = false;
        let buffer = '';
        // let conversationIdReceived = false;
        
        while (!done) {
          const { value, done: doneReading } = await reader.read();
          done = doneReading;
          if (value) {
            buffer += decoder.decode(value, { stream: true });
            // ‰ª•Êç¢Ë°åÂàÜÂâ≤ÊµÅÂºèÁâáÊÆµ
            let lines = buffer.split('\n');
            buffer = lines.pop();
            for (const line of lines) {
              if (!line.trim()) continue;
              if (line.startsWith('event: ping')) continue; // Ë∑≥ËøáÂøÉË∑≥ÂåÖ
              // ÂÖºÂÆπ SSE Ê†ºÂºè: data: xxx
              let content = line;
              if (line.startsWith('data:')) content = line.replace(/^data:\s*/, '');
              try {
                const json = JSON.parse(content);
                if (json.answer) {
                  botMsg.text += json.answer;
                } else if (json.response) {
                  botMsg.text += json.response;
                } else if (typeof json === 'string') {
                  botMsg.text += json;
                }
                // console.log(json);
                this.conversation_id = json.conversation_id;
                // Ëé∑Âèñconversation_idÔºàÂè™Âú®Á¨¨‰∏ÄÊ¨°Êî∂Âà∞Êó∂ËÆæÁΩÆÔºâ
                // if (!conversationIdReceived && json.conversation_id) {
                //   this.conversation_id = json.conversation_id;
                //   conversationIdReceived = true;
                // }
              } catch {
                botMsg.text += content;
              }
              this.$forceUpdate();
              this.$nextTick(() => {
                const chatHistory = this.$el.querySelector('.chat-history');
                if (chatHistory) chatHistory.scrollTop = chatHistory.scrollHeight;
              });
            }
          }
        }
        // Â§ÑÁêÜÊúÄÂêé‰∏ÄÊÆµ
        if (buffer && buffer.trim()) {
          try {
            const json = JSON.parse(buffer);
            if (json.answer) {
              botMsg.text += json.answer;
            } else if (json.response) {
              botMsg.text += json.response;
            } else if (typeof json === 'string') {
              botMsg.text += json;
            }
          } catch {
            botMsg.text += buffer;
          }
          this.$forceUpdate();
        }
        botResponse = botMsg.text;
      } catch (e) {
        console.error('APIËØ∑Ê±ÇÈîôËØØ:', e);
        botMsg.text = 'Êú∫Âô®‰∫∫ÊúçÂä°ÊöÇÊó∂‰∏çÂèØÁî®ÔºåËØ∑Á®çÂêéÂÜçËØï„ÄÇ';
        botResponse = botMsg.text;
      } finally {
        this.loading = false;
        // Êú∫Âô®‰∫∫ÂõûÂ§çÂêéÔºåÊãâÂèñÂèçÈ¶à
        console.log(botResponse);
        if(this.currentTraining === 'ÂºïÂØºÊÄßÂØπËØù'&& botResponse!="Âõ†ÊÇ®ËøûÁª≠‰ΩøÁî®Êö¥ÂäõÊ≤üÈÄöÔºåËØ∑ÊÇ®ÂÜ∑Èùô‰πãÂêéÂÜçËøõË°åÂõûÂ§ç„ÄÇTipsÔºöÊé®Ëçê‰ΩøÁî®NVCÁ≠ñÁï•ËøõË°åÂõûÂ§ç„ÄÇ"){
          this.get_score_feedback(title, text, botResponse);
          this.get_teach_feedback(title, text, botResponse);
        }
       }
    },
    async get_score_feedback(title, user_response, bot_response) {
      this.scoreFeedbackLoading = true; // Êñ∞Â¢û
      try {
        const res = await axios.post('http://dify.ai-role.cn/v1/chat-messages', {
          query: 'score feedback',
          inputs: {
            title:title,
            user_response:user_response,
            bot_response:bot_response
          },
          response_mode: "blocking",
          user: "abc-123",
          conversation_id:this.score_id
        }, {
          headers: {
            'Authorization': 'Bearer app-XPpxMLpT4EmwJ2VwXwvmOBPt',
            'Content-Type': 'application/json'
          }
        });
        // console.log(res);
        console.log(res.data.answer)
        this.scoreFeedback = res.data.answer;
        this.score_id=res.data.conversation_id;
      } catch (e) {
        this.scoreFeedback = 'ËØÑÂàÜÂèçÈ¶àËé∑ÂèñÂ§±Ë¥•';
      } finally {
        this.scoreFeedbackLoading = false; // Êñ∞Â¢û
      }
    },
    async get_teach_feedback(title, user_response, bot_response) {
      this.teachFeedbackLoading = true; // Êñ∞Â¢û
      // console.log(this.teach_id)
      try {
        const res = await axios.post('http://dify.ai-role.cn/v1/chat-messages', {
          query: 'teach feedback',
          inputs: {
            title: title,
            user_response: user_response,
            bot_response: bot_response
          },
          response_mode: "blocking",
          user: "abc-123",
          conversation_id:this.teach_id
        }, {
          headers: {
            'Authorization': 'Bearer app-YhRXu8JFuHs9deDyR8AJwYrC',
            'Content-Type': 'application/json'
          }
        });
        // ÂÅáËÆæËøîÂõûÂ≠óÁ¨¶‰∏≤ answer
        const str = res.data.answer || '';
        console.log('ÊïôÂ≠¶ÂèçÈ¶àÂéüÂßãÊï∞ÊçÆ:', res.data.answer);
        this.teach_id=res.data.conversation_id;
        // ÂàÜÂâ≤Â≠óÁ¨¶‰∏≤‰∏∫4‰∏™Âç°Áâá
        const cardDefs = [
          { title: 'NVC', icon: 'üí°', colorClass: 'teach-yellow' },
          { title: 'Proposal', icon: '‚≠ê', colorClass: 'teach-green' },
          { title: 'Concession', icon: 'üìà', colorClass: 'teach-blue' },
          { title: 'Promise', icon: 'üéØ', colorClass: 'teach-purple' }
        ];
        
        // Â∞ùËØïÂ§öÁßçÂåπÈÖçÊñπÂºè
        let result = [];
        
        // ÊñπÂºè1: ‰ΩøÁî®Ê≠£ÂàôË°®ËææÂºèÂåπÈÖç
        const regex = /(NVC|Proposal|Concession|Promise)Ôºö(.*?)(?=(NVC|Proposal|Concession|Promise)Ôºö|$)/gs;
        let match;
        while ((match = regex.exec(str)) !== null) {
          const idx = cardDefs.findIndex(c => c.title === match[1]);
          if (idx !== -1) {
            result[idx] = {
              ...cardDefs[idx],
              content: match[2].trim()
            };
          }
        }
        
        // ÊñπÂºè2: Â¶ÇÊûúÊ≠£ÂàôÂåπÈÖçÂ§±Ë¥•ÔºåÂ∞ùËØïÊåâË°åÂàÜÂâ≤
        if (result.length === 0) {
          const lines = str.split('\n').filter(line => line.trim());
          lines.forEach(line => {
            const colonIndex = line.indexOf('Ôºö');
            if (colonIndex > 0) {
              const title = line.substring(0, colonIndex).trim();
              const content = line.substring(colonIndex + 1).trim();
              const idx = cardDefs.findIndex(c => c.title === title);
              if (idx !== -1) {
                result[idx] = {
                  ...cardDefs[idx],
                  content: content
                };
              }
            }
          });
        }
        
        console.log('Ëß£ÊûêÂêéÁöÑÊïôÂ≠¶ÂèçÈ¶à:', result);
        
        // ‰øùËØÅÈ°∫Â∫èÂíåÂÆåÊï¥ÊÄß
        this.teachFeedback = cardDefs.map((def, i) => result[i] || { ...def, content: 'ÊöÇÊó†ÂèçÈ¶àÂÜÖÂÆπ' });
        console.log(this.teachFeedback)
      } catch (e) {
        this.teachFeedback = [];
      } finally {
        this.teachFeedbackLoading = false; // Êñ∞Â¢û
      }
    },
    openSettings() {
      this.showSettingsModal = true;
    },
    closeSettings() {
      this.showSettingsModal = false;
    },
    saveSettings() {
      this.showSettingsModal = false;
    },
    resetAll() {
      this.messages = [
        {
          role: 'bot',
          text: '‰Ω†Â•ΩÔºÅÊàëÊòØ‰Ω†ÁöÑConflict SimulationÂä©Êâã„ÄÇÊàëÂèØ‰ª•Â∏ÆÂä©‰Ω†Ê®°ÊãüÂíåÂàÜÊûêÂêÑÁßçÂÜ≤Á™ÅÂú∫ÊôØÔºåÂπ∂Êèê‰æõ‰∏™ÊÄßÂåñÁöÑÊåáÂØº„ÄÇ'
        }
      ];
      this.input = '';
