import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../views/Main.vue'
import Evaluation from '../views/evaluation.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Main',
    component: Main
  },
  {
    path: '/evaluation',
    name: 'Evaluation',
    component: Evaluation,
    props: true
  }
]

const router = new VueRouter({
  routes
})

export default router


