<script>
import * as echarts from 'echarts';
import axios from 'axios';

export default {
  name: 'Evaluation',
  data() {
    return {
      radarIndicator: [],
      radarData: [],
      currentTraining: this.$route.query.currentTraining || '',
      currentTitle: this.$route.query.currentTitle || '',
      conversation_id: this.$route.query.conversation_id || '',
      evaluationReasons: ['', '', '', '', ''], // Â≠òÂÇ®5‰∏™Áª¥Â∫¶ÁöÑËØÑÂàÜÁêÜÁî±
      isLoading: true, // Ê∑ªÂä†Âä†ËΩΩÁä∂ÊÄÅ
      suggestion: '' // Êñ∞Â¢ûÂª∫ËÆÆÂ≠óÊÆµ
    }
  },
  mounted() {
    this.getRadarData();
  },
  methods: {
    async getRadarData() {
      this.isLoading = true; // ÂºÄÂßãÂä†ËΩΩ
      try {
        const res = await this.axios.post('http://dify.ai-role.cn/v1/chat-messages', {
          query: 'get evaluation',
          inputs: {
              training: this.currentTraining, // ‰ΩøÁî®Âä®ÊÄÅtraining
              title: this.currentTitle,
              quit:1
            },
          response_mode: "blocking",
          user: "abc-123",
          conversation_id: this.conversation_id
        }, {
          headers: {
            'Authorization': 'Bearer app-1FfMKOZZ12LF8HXlpXdTopPG',
            'Content-Type': 'application/json'
          }
        });
        console.log(res);
        const data = res.data.answer || '';
        console.log('Èõ∑ËææÂõæÊï∞ÊçÆ:', data);
        
        // Ëß£ÊûêËøîÂõûÁöÑÂ≠óÁ¨¶‰∏≤Ê†ºÂºèÊï∞ÊçÆ
        this.parseEvaluationData(data);
        // Á≠âÂæÖDOMÊõ¥Êñ∞ÂêéÂÜçÂàùÂßãÂåñÈõ∑ËææÂõæ
        this.$nextTick(() => {
          this.initRadarChart();
        });
      } catch (e) {
        console.error('Ëé∑ÂèñÈõ∑ËææÂõæÊï∞ÊçÆÂ§±Ë¥•:', e);
        // ËØ∑Ê±ÇÂ§±Ë¥•Êó∂Áî®ÈªòËÆ§Êï∞ÊçÆ
        this.radarIndicator = [
          { name: 'ÂÜ≤Á™ÅËΩ¨Âåñ', max: 5 },
          { name: 'Á≠ñÁï•‰ΩøÁî®', max: 5 },
          { name: 'ÊÉÖÁª™Ë∞ÉËäÇ', max: 5 },
          { name: '‰∏ªÂä®Ë°®Ëææ', max: 5 },
          { name: 'ÂÖ≥Á≥ªÁª¥Êä§', max: 5 }
        ];
        this.radarData = [2, 2, 2, 2, 2];
        this.evaluationReasons = [
          'ÈªòËÆ§ËØÑÂàÜÁêÜÁî±ÔºöÂÜ≤Á™ÅËΩ¨ÂåñË°®Áé∞ËâØÂ•Ω',
          'ÈªòËÆ§ËØÑÂàÜÁêÜÁî±ÔºöÁ≠ñÁï•‰ΩøÁî®ÂæóÂΩì',
          'ÈªòËÆ§ËØÑÂàÜÁêÜÁî±ÔºöÊÉÖÁª™Ë∞ÉËäÇÁ®≥ÂÆö',
          'ÈªòËÆ§ËØÑÂàÜÁêÜÁî±Ôºö‰∏ªÂä®Ë°®ËææÊ∏ÖÊô∞',
          'ÈªòËÆ§ËØÑÂàÜÁêÜÁî±ÔºöÂÖ≥Á≥ªÁª¥Êä§ÊúâÊïà'
        ];
        this.suggestion = 'Âª∫ËÆÆÔºöÂ∞ùËØïË°®ËææËá™Â∑±ÁöÑÊÑüÂèóÂíåÁêÜÁî±ÔºåÂπ∂ÊèêÂá∫ÂÖ∑‰ΩìÊâøËØ∫ÊàñÊîπËøõÊé™ÊñΩÔºå‰øÉËøõÂª∫ËÆæÊÄßÂØπËØù„ÄÇ';
        // Á≠âÂæÖDOMÊõ¥Êñ∞ÂêéÂÜçÂàùÂßãÂåñÈõ∑ËææÂõæ
        this.$nextTick(() => {
          this.initRadarChart();
        });
      } finally {
        this.isLoading = false; // ÁªìÊùüÂä†ËΩΩ
      }
    },
    parseEvaluationData(data) {
      // ËÆæÁΩÆÈªòËÆ§ÁöÑÈõ∑ËææÂõæÁª¥Â∫¶
      this.radarIndicator = [
        { name: 'ÂÜ≤Á™ÅËΩ¨Âåñ', max: 5 },
        { name: 'Á≠ñÁï•‰ΩøÁî®', max: 5 },
        { name: 'ÊÉÖÁª™Ë∞ÉËäÇ', max: 5 },
        { name: '‰∏ªÂä®Ë°®Ëææ', max: 5 },
        { name: 'ÂÖ≥Á≥ªÁª¥Êä§', max: 5 }
      ];
      // ÂàùÂßãÂåñËØÑÂàÜÊï∞ÊçÆ
      this.radarData = [0, 0, 0, 0, 0];
      this.evaluationReasons = ['', '', '', '', ''];
      // Ëß£ÊûêËØÑÂàÜÈÉ®ÂàÜ
      const scorePattern = /(\d+)\.\s*([^:]+):\s*(\d+)/g;
      let match;
      while ((match = scorePattern.exec(data)) !== null) {
        const dimensionName = match[2].trim();
        const score = parseInt(match[3]);
        const dimensionIndex = this.radarIndicator.findIndex(item => item.name === dimensionName);
        if (dimensionIndex !== -1) {
          this.radarData[dimensionIndex] = score;
        }
      }
      // Ëß£ÊûêËØÑÂàÜÁêÜÁî±ÈÉ®ÂàÜ
      // 1. ÊâæÂà∞"ËØÑÂàÜÁêÜÁî±Ôºö"ÂêéÈù¢ÁöÑÂÜÖÂÆπ
      const reasonStart = data.indexOf('ËØÑÂàÜÁêÜÁî±Ôºö');
      if (reasonStart !== -1) {
        // ÊâæÂà∞"Âª∫ËÆÆÔºö"Êàñ"ÊîπËøõÂª∫ËÆÆÔºö"ÁöÑ‰ΩçÁΩÆÔºåÁî®‰∫éÂàÜÂâ≤ÁêÜÁî±ÂíåÂª∫ËÆÆ
        const suggestionStart = data.indexOf('Âª∫ËÆÆÔºö');
        const improvementStart = data.indexOf('ÊîπËøõÂª∫ËÆÆÔºö');
        let reasonEnd = data.length;
        
        if (suggestionStart !== -1 && suggestionStart > reasonStart) {
          reasonEnd = suggestionStart;
        } else if (improvementStart !== -1 && improvementStart > reasonStart) {
          reasonEnd = improvementStart;
        }
        
        let reasonText = data.slice(reasonStart + 5, reasonEnd).trim();
        // 2. Áî®Ê≠£ÂàôÂàÜÂâ≤Âá∫ÊØè‰∏ÄÊù°ÁêÜÁî±ÔºàÊîØÊåÅÂ§öË°åÔºâ
        // ÂåπÈÖç"1. xxx\n2. xxx\n..."
        const reasonArr = reasonText.split(/(?=\d+\. )/).filter(Boolean);
        reasonArr.forEach((item, idx) => {
          // Âè™ÂèñÂâç5Êù°
          if (idx < 5) {
            // ÂéªÊéâÂâçÁºÄ"1. "„ÄÅ"2. "Á≠â
            const content = item.replace(/^\d+\.\s*/, '').trim();
            // Ëøõ‰∏ÄÊ≠•ÂéªÊéâÁª¥Â∫¶Âêç"ÂÜ≤Á™ÅËΩ¨ÂåñÔºö"Á≠â
            const colonIdx = content.indexOf('Ôºö');
            let reason = content;
            if (colonIdx !== -1) {
              reason = content.slice(colonIdx + 1).trim();
            }
            this.evaluationReasons[idx] = reason;
          }
        });
      }
      // Ëß£ÊûêÂª∫ËÆÆÈÉ®ÂàÜ
      const suggestionStart = data.indexOf('ÊîπËøõÂª∫ËÆÆÔºö');
      if (suggestionStart !== -1) {
        this.suggestion = data.slice(suggestionStart + 5).trim();
      } else {
        // Â∞ùËØïÂÖ∂‰ªñÊ†ºÂºèÁöÑÂª∫ËÆÆÊèêÂèñ
        const suggestionPatterns = [
          /Âª∫ËÆÆÔºö\s*(.+?)(?=\n\d+\.|$)/s,
          /ÊîπËøõÂª∫ËÆÆÔºö\s*(.+?)(?=\n\d+\.|$)/s,
          /Âª∫ËÆÆ\s*(.+?)(?=\n\d+\.|$)/s
        ];
        
        for (const pattern of suggestionPatterns) {
          const match = data.match(pattern);
          if (match) {
            this.suggestion = match[1].trim();
            break;
          }
        }
        
        // Â¶ÇÊûúËøòÊòØÊ≤°ÊâæÂà∞ÔºåÂ∞ùËØïÊèêÂèñÊúÄÂêé‰∏Ä‰∏™ÊÆµËêΩ‰Ωú‰∏∫Âª∫ËÆÆ
        if (!this.suggestion) {
          const lines = data.split('\n').filter(line => line.trim());
          const lastLine = lines[lines.length - 1];
          if (lastLine && !lastLine.match(/^\d+\./)) {
            this.suggestion = lastLine.trim();
          }
        }
      }
      // ÂÖúÂ∫ïÔºöÂ¶ÇÊûúÊúâÁ©∫ÁêÜÁî±ÔºåÂ°´ÂÖÖÈªòËÆ§
      this.evaluationReasons = this.evaluationReasons.map((r, i) => r || `ÊöÇÊó†ËØÑÂàÜÁêÜÁî±`);
      // ÊéßÂà∂Âè∞ËæìÂá∫Ë∞ÉËØï
      console.log('Ëß£ÊûêÂêéÁöÑÈõ∑ËææÂõæÊï∞ÊçÆ:', {
        indicator: this.radarIndicator,
        data: this.radarData,
        reasons: this.evaluationReasons
      });
    },
    initRadarChart() {
      const chartDom = document.getElementById('radarChart');
      if (!chartDom) {
        console.warn('Èõ∑ËææÂõæDOMÂÖÉÁ¥†‰∏çÂ≠òÂú®ÔºåË∑≥ËøáÂàùÂßãÂåñ');
        return;
      }
      
      const myChart = echarts.init(chartDom);
      const option = {
        tooltip: {},
        radar: {
          indicator: this.radarIndicator,
          splitLine: {
            lineStyle: {
              color: '#e0d7f7',
            }
          },
          splitArea: {
            areaStyle: {
              color: ['#f6f0ff', '#f3eaff'],
            }
          },
          axisLine: {
            lineStyle: {
              color: '#a084e8',
            }
          },
          name: {
            color: '#a084e8',
            fontSize: 16
          }
        },
        series: [{
          name: 'ÂæóÂàÜ',
          type: 'radar',
          areaStyle: {
            color: 'rgba(160, 132, 232, 0.3)'
          },
          lineStyle: {
            color: '#a084e8',
            width: 3
          },
          itemStyle: {
            color: '#a084e8',
            borderColor: '#fff',
            borderWidth: 2
          },
          data: [
            {
              value: this.radarData,
              name: 'Ê†∑‰æã',
              areaStyle: {
                color: 'rgba(160, 132, 232, 0.3)'
              },
              lineStyle: {
                color: '#a084e8',
                width: 3
              },
              itemStyle: {
                color: '#a084e8',
                borderColor: '#fff',
                borderWidth: 2
              }
            }
          ]
        }]
      };
      myChart.setOption(option);
    }
  }
}
</script>